body {
  background-image: url("./assets/background/background.jpg");
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  margin: 0;
  outline: 0;
  padding: 0;
}

.heading {
  margin-top: 180px;
  margin-bottom: 0px;
  font-family: 'Asul',
    sans-serif;
  font-size: 60px;
  color: white;
  opacity: 0.93;
}

.heading-mobile-screen {
  display: none;
  margin-top: 60px;
  font-size: 42px;
  font-family: 'Asul',
    sans-serif;
  font-size: 60px;
  color: white;
  opacity: 0.93;
}

.text {
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: 'Alegreya Sans',
    sans-serif;
  font-size: 20px;
  font-weight: lighter;
  white-space: pre-line;
  color: #C0C6CC;
}

.text-tablet-screen {
  display: none;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: 'Alegreya Sans',
    sans-serif;
  font-size: 20px;
  font-weight: lighter;
  white-space: pre-line;
  color: #C0C6CC;
}

.text-mobile-screen {
  display: none;
}

.button-format {
  padding: 12px;
  font-size: 30px;
  width: 50px;
  height: 50px;
  text-decoration: none;
  margin: 0px 4px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
}

.hv:hover {
  background-color: rgba(255, 255, 255, 0.20);
}

/*Media query for tablet sized devices*/

@media only screen and (max-width: 680px) {

  body {
    min-height: 115vh;
    background-size: cover;
    overflow: hidden;
  }

  .heading {
    margin-top: 180px;
    font-size: 38px;
  }

  .heading-mobile-screen {
    display: none;
  }

  .text {
    display: none;
  }

  .text-tablet-screen {
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: 'Alegreya Sans',
      sans-serif;
    font-size: 18px;
    font-weight: lighter;
    white-space: pre-line;
    color: #C0C6CC;
  }

  .text-mobile-screen {
    display: none;
  }

}

/*Media query for mobile devices*/

@media only screen and (max-width: 425px) {
  body {
    min-height: 135vh;
    background-size: cover;
    overflow: hidden;
  }

  .heading-mobile-screen {
    display: block;
    margin-top: 80px;
    font-size: 35px;
  }

  .heading {
    display: none;
  }

  .text {
    display: none;
  }

  .text-tablet-screen {
    display: none;
  }

  .text-mobile-screen {
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: 'Alegreya Sans',
      sans-serif;
    font-size: 18px;
    font-weight: lighter;
    white-space: pre-line;
    color: #C0C6CC;
  }

}